import React, {Component} from 'react';
import {Route, NavLink, HashRouter} from "react-router-dom";

class Notification extends React.Component{
    constructor(props){
        super(props)
        this.state={
            modal: true
        }
    }
    render(){
        return(
            <div>
                <span className="notification" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-bell"></i></span>

                {/* <!-- Modal --> */}
             {this.state.modal && <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Cab Booking</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body desBody">
                        Time to start for your event
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        <NavLink to="/cabBooking"><button type="button" class="btn btn-primary">Book a Ride</button></NavLink>
                    </div>
                    </div>
                </div>
                </div>}
            </div>
        );
    }
}

export default Notification;